@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@500&display=swap');
html {
    overflow: hidden;
    height: 100%;
}

body {
    margin: 0;
    padding: 0;
    overflow: hidden;
    height: 100%;
    font-family: 'Roboto', sans-serif;
}

div {
    margin: 0;
    padding: 0;
}


/* #bp3d-js-app {
    overflow: hidden;
} */

#bp3djs-viewer2d,
#bp3djs-viewer3d {
    position: absolute;
    width: 100vw;
    height: 100vh;
    top: 0;
    left: 0;
}
.wrapper-add-items{
    color:#fff;
    text-align: center;
}
#bp3djs-controls {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1;
}
.category-items{
    display: block;
    align-items: center;
    width: 24%;
    margin-bottom: 20px;
    cursor: pointer;
    margin-left: auto;
    margin-right: auto;
}
.qs_button,
.qs_file_chooser_label{
    width: 100%;
    width: -moz-available;          /* WebKit-based browsers will ignore this. */
    width: -webkit-fill-available;  /* Mozilla-based browsers will ignore this. */
    width: fill-available;
    display: block;
    text-align: center;
}
.qs_button:hover,
.qs_file_chooser_label:hover{
    cursor: pointer;
    background-color:#ccc;
}
.qs_label{
    font-family: 'Roboto', sans-serif;
    text-align: center;
}
.qs_label b{
    display: block;
}
.sub-category-items{
    background: #fff;
    width: 100%;
    height: 100vh;
    display: none;
    position: absolute;
    left: 0;
    top: 0;
}
.sub-category-items h3{
    color: black;
    font-size: 30px;
    text-align: center;
}
.back-btn{
    width: 200px;
    height: 30px;
    cursor: pointer;
    background: #ab1a1f;
    position: absolute;
    top: 10px;
    left: 10px;
    padding-top: 7px;
    z-index: 9999;
}
.item{
    width: 300px;
    border:1px solid #ccc;
    cursor: pointer;
    font-size: 14px;
    margin-left: 10px;
}
.item img{
    width: 100%;
    height: 200px;
}
.item p,
.item-name{
    color: black;
}
.item p{
    line-height: 10px;
}
.item-name{
    font-size: 16px;
    font-weight: bold;
}
.print-area{
    color: #ab1a1f !important;
}
.item-wrap{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    text-align: center;
    align-items: center;
}
.hover-link{
    color: #fff;
    text-decoration: none;
}
.hover-link:hover{
    color: #ccc;
    text-decoration: underline;
}
.sub-sub-category{
    display: none;
    background: #ccc;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    position: absolute;
    cursor: default;
}
.sub-sub-category ul{
    list-style: none;
}
.item-menu{
    position: absolute;
}
.item-menu ul{
    list-style: none;
    background: #eeeeee;
    padding-bottom: 10px;
    padding-right: 20px;
    margin-left: -20px;
    margin-top: 0;
}
.item-menu ul li{
    margin-top: 10px;
}
.item-menu ul li a{
    color: #000;
    font-size: 14px;
    text-decoration: none;
}
.dropdown-level1{
    display: none;
}
.wrap_popup{
	display:none;
	position: fixed;
	z-index: 4;
	left: 0;
	top: 0;
	height:100%;
	width:100%;
	overflow:auto;
    background-color: rgba(0,0,0,0.6);
}

.popup{
	height:auto;
	width:80%;
	max-width:600px;
	margin:5% auto;
	display:block;
	border-radius:8px;
	overflow:hidden;
	-moz-box-shadow: 0 0 5px #000;
	-webkit-box-shadow: 0 0 5px #000;
	box-shadow: 0 0 5px #000;
}

.title{
	width:100%;
	display:block;
	background:#71BBF2;
	color:#0C63A4;
	font-weight:700;
    padding:5px;
    text-align: center;
}

.title p{
	margin:5px;
}

.box{
	width:100%;
	display:block;
	background:#DFDFDF;
	padding:5px;
	color:#3D3D3D;
}

.box p{
	margin:5px;
}

.box button{
	padding:10px;
	margin:10px 0px 10px 0px;
	background:#DFDFDF;
	border:3px solid #0C63A4;
    border-radius:5px;
    cursor: pointer;
}
#nav {
    padding: 20px 0;
  }
  
  ul {
    list-style: none;
  }
  
  a {
    text-decoration: none;
  }
  
  .menu li a {
    display: block;
    padding: 10px 30px;
  }
  
  .menu li {
    background-color: #ddd;
  }
  
  .menu-item {
    margin-right: 2px;
    position: relative;
  }
  
  .menu {
    display: flex;
  }
  
  .submenu,
  .submenu-level2 {
    position: absolute;
    display: none;
  }
  
  .submenu-item {
    margin-top: 1px;
  }
  .menu{
    display:block;
}
.menu li{
    display: inline-block;
    position: relative;
    z-index:100;
}
.menu li a {
    font-weight:600;
    text-decoration:none;
    padding:11px;
    display:block;
    color:#ffffff;
 
    -webkit-transition: all 0.2s ease-in-out 0s;
    -moz-transition: all 0.2s ease-in-out 0s;
    -o-transition: all 0.2s ease-in-out 0s;
    -ms-transition: all 0.2s ease-in-out 0s;
    transition: all 0.2s ease-in-out 0s;
}
.menu li a:hover,
.menu li:hover > a{
    color:#ffffff;
    background:#ab1a1f;
}
.menu ul {
    display: none;
    margin: 0;
    padding: 0;
    width: 150px;
    position: absolute;
    top: 39px;
    left: 0px;
    background: #ffffff;
}
/* показываем меню второго уровня при наведении */
.menu li:hover > ul{
    display: block;
}
.menu ul li {
    display:block;
    float: none;
    background:none;
    margin:0;
    padding:0;
}
.menu ul li a {
    font-size:12px;
    font-weight:normal;
    display:block;
    color:#797979;
    border-left:3px solid #ffffff;
    background:#ffffff;
}
.menu ul li a:hover,
.menu ul li:hover > a{
    background:#f0f0f0;
    border-left:3px solid #ab1a1f;
    color:#797979;
}
.menu ul ul {
    left: 149px;
    top: 0px;
}
.item-menu{
    z-index: 99;
}